import { CloseOutlined, InboxOutlined } from "@ant-design/icons";
import { Button, Drawer, Form, Input, Select, Space, UploadProps, message } from "antd";
import Dragger from "antd/es/upload/Dragger";

interface Props {
    open: boolean;
    onSuccess: () => void;
    onClose: () => void
}

const AddSourcemapPage: React.FC<Props> = (props: Props) => {
    const [form] = Form.useForm();

    const onFinish = async (values: any) => {
        try {
            // await addApp({
            //     name: values.name,
            //     type: values.type
            // });
            //清空form
            form.resetFields();
            message.success("创建成功");
            props.onSuccess();
        } catch (error) {
            console.log(error);
        }
    };

    const uploadProps: UploadProps = {
        accept: '.map',
        name: 'file',
        multiple: false,
        action: 'http://localhost:3000/file/upload',
        onChange(info) {
            const { status } = info.file;
            if (status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (status === 'done') {
                message.success(`${info.file.name} 文件上传成功`);
            } else if (status === 'error') {
                message.error(`${info.file.name} 文件上传失败`);
            }
        },
        onDrop(e) {
            console.log('Dropped files', e.dataTransfer.files);
        },
    };

    const onChange = (value: string) => {
        console.log(`selected ${value}`);
    };

    const onSearch = (value: string) => {
        console.log('search:', value);
    };

    return (
        <Drawer
            title="上传SourceMap"
            placement='right'
            width={500}
            closable={false}
            open={props.open}
            extra={
                <Space>
                    <CloseOutlined onClick={props.onClose} />
                </Space>
            }
        >
            <Form
                layout="vertical"
                form={form}
                onFinish={onFinish}
                initialValues={{ type: 1 }}
            >
                <Form.Item label="js sourcemap文件名" name="name" rules={[{ required: true }]}>
                    <Input />
                </Form.Item>
                <Form.Item label="上传 Sourcemap 文件" rules={[{ required: true }]}>
                    <Form.Item name="dragger" valuePropName="fileList" noStyle>
                        <Dragger {...uploadProps}>
                            <p className="ant-upload-drag-icon">
                                <InboxOutlined />
                            </p>
                            <p className="ant-upload-text">Click or drag file to this area to upload</p>
                            <p className="ant-upload-hint">
                                Support for a single or bulk upload. Strictly prohibited from uploading company data or other
                                banned files.
                            </p>
                        </Dragger>
                    </Form.Item>
                </Form.Item>
                <Form.Item label="Release" name="release" rules={[{ required: true }]}>
                    <Select
                        showSearch
                        allowClear
                        placeholder="选择或输入对应版本"
                        optionFilterProp="children"
                        onChange={onChange}
                        onSearch={onSearch}
                        filterOption={(input, option) =>
                            (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                        }
                        options={[
                            {
                                value: 'jack',
                                label: 'Jack',
                            },
                            {
                                value: 'lucy',
                                label: 'Lucy',
                            },
                            {
                                value: 'tom',
                                label: 'Tom',
                            },
                        ]}
                    />
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit">
                        确定
                    </Button>
                </Form.Item>
            </Form>
        </Drawer>
    );
}

export default AddSourcemapPage;